import React from 'react';

class Intro extends React.Component {
  render() {
    return (
      <article>
        <h2>Hello.</h2>
        I've been interested in coding ever since the age of seven, when I first started writing programs in BASIC on my dad's Amiga 500. When my family got our first PC a few years later, the first thing I made with it was my first website. Within a year, I had taught myself HTML and CSS and done two weeks' work experience with a web design startup where I learned basic Flash and Photoshop.
        <br /><br />
        <h2>I'm a front end developer.</h2>
        In 2017, I was awarded a scholarship at Northcoders, funded by EY's Women. Fast Forward initiative, to train as a JavaScript developer. I learned many new skills in an incredibly short amount of time, and I've continued to learn and grow as a React developer in my first position since graduating. I'm also proud to be able to return to Northcoders as a tutor this summer.
        <br /><br />
        <h2>Here's what I can do.</h2>
        <ul className="skill-list">
          <li>ES6</li>
          <li>React</li>
          <li>Redux</li>
          <li>HTML5</li>
          <li>CSS3</li>
          <li>Redux-Saga</li>
          <li>Redux-Thunk</li>
          <li>Webpack</li>
          <li>Jest</li>
        </ul>
      </article>
    );
  }
}

export default Intro;


