import React from 'react';
import Project from './Project'

class Portfolio extends React.Component {
  render() {
    return (
      <article className="portfolio">

        <h2>
          Here are some examples of my work.
        </h2>

        <Project
          link={'https://dentednerd.github.io/NCNews/'}
          image={'./NCNewsShot.png'}
          title={'Northcoders News'}
          caption={'A Reddit clone built in Express, Mongo, React, Redux and Bulma.'}
        />

        <Project
          link={'https://m.me/Charcodeizard'}
          image={'./CharCodeShot.png'}
          title={'CharCodeizard'}
          caption={'An award-winning Facebook bot using Node and Express.'}
        />

        <Project
          link={'https://dentednerd.github.io/Trell-Jo/'}
          image={'./TrellJoShot.png'}
          title={'TrellJo'}
          caption={'A Trello clone built in React and Bulma.'}
        />

        <Project
          link={'https://dentednerd.github.io/Northwitter/'}
          image={'./NorthwitterShot.png'}
          title={'Northwitter'}
          caption={'A Twitter clone built in React and Bootstrap.'}
        />

        <Project
          link={'https://github.com/dentednerd/LowbarAdv'}
          image={'./LowBarShot.png'}
          title={'Lowbar'}
          caption={'Pure JavaScript implementations of Underscore functions.'}
        />

        <Project
          link={'https://github.com/northcode-ninjas'}
          image={'./CodeNinjasShot.png'}
          title={'Code Ninjas'}
          caption={'An educational game built in Mongo, Express, React, Redux and Bulma.'}
        />

      </article>
    );
  }
}

export default Portfolio;


